import dayjs, { Dayjs } from "dayjs";
import "./reports.css";
import * as React from "react";

import DatePicker from "react-datepicker";
import { useState } from "react";
import "react-datepicker/dist/react-datepicker.css";

import { productActions } from "../../actions/product/product";
import { connect } from "react-redux";
import ReactToPrint, { PrintContextConsumer } from "react-to-print";
import { PrintBill } from "../home/printBill";
class Reports extends React.Component {
  constructor(props) {
    super(props);
    this.reff = React.createRef();
    this.state = {
      startDate: new Date(),
      endDate: new Date(),
      todayBill: 0,
      total: 0,
      data: [],
    };
    this.props.getBillToday({
      startDate: new Date().toISOString().split("T")[0],
      endDate: "",
    });
    this.props.getAll({  startDate: new Date().toISOString().split("T")[0],
    endDate: "",});
  }

  async changeDate(date) {
    const { startDate, endDate } = this.state;
    const sdate = date.toISOString().split("T")[0];
    // const edate=endDate.toISOString().split("T")[0]

    await this.props.getAll({ startDate: sdate, endDate: "" });

  }


  handleAfterPrint = () => {
    console.log("`onAfterPrint` called"); // tslint:disable-line no-console
  };

  handleBeforePrint = () => {
    console.log("`onBeforePrint` called"); // tslint:disable-line no-console
  };

  handleOnBeforeGetContent = () => {
    console.log("`onBeforeGetContent` called"); // tslint:disable-line no-console
    this.setState({ text: "Loading new text...", isLoading: true });

    return new Promise((resolve) => {
      setTimeout(() => {
        this.setState(
          { text: "New, Updated Text!", isLoading: false },
          resolve
        );
      }, 2000);
    });
  };

  setComponentRef = (ref) => {
    this.componentRef = ref;
  };

  reactToPrintContent = () => {
    return this.componentRef;
  };
  print(item) {
    this.setState({ data: item });
    this.reff.current.click();
  }
  render() {
    const { startDate, todayBill, endDate, total } = this.state;
    const { product } = this.props;
    return (
      <div className="container">
        <div className="page-title">Reports</div>
        <div className="report-container">
          <div className="calender-section">
            <div className="today-sale">
              <div className="title">Today sales</div>
              <div className="total">{Number(product.bill_today_amount).toFixed(2)} Rs</div>
            </div>
            <DatePicker
              selected={startDate}
              // selectsRange
              // onChange={(date) => this.changeDate(date)}
              // startDate={startDate}
              // endDate={endDate}
              onChange={(date) => this.changeDate(date)}
              // selectsRange
              inline
            />
          </div>
          <div className="report-section">
            <div className="total-amount">
              <div className="title">Total</div>
              <div className="amount">{Number(product.bill_total).toFixed(2)} Rs</div>
            </div>
            <div className="table-section">
              <table>
                <tr>
                  <th>Bill ID</th>
                  <th>Amount</th>
                </tr>
                {product.bill_data &&
                  product.bill_data.map((item, i) => (
                    <tr>
                      <td>{item.billId}</td>
                      <td>{Number(item.amount).toFixed(2)}</td>
                    </tr>
                  ))}
              </table>
            </div>
          </div>
        </div>

        {/* <div>
          <ReactToPrint
            content={this.reactToPrintContent}
            documentTitle="AwesomeFileName"
            onAfterPrint={this.handleAfterPrint}
            onBeforeGetContent={this.handleOnBeforeGetContent}
            onBeforePrint={this.handleBeforePrint}
            removeAfterPrint
            trigger={() => <a ref={this.reff}></a>}
          />

          {this.state.isLoading && (
            <p className="indicator">onBeforeGetContent: Loading...</p>
          )}
          <div className="d-none">
            <PrintBill ref={this.setComponentRef} text={this.state.data}></PrintBill>
          </div>
        </div> */}
      </div>
    );
  }
}
function mapState(state) {
  const { product } = state;

  return { product };
}

const actionCreators = {
  getAll: productActions.getAllBill,
  getBillToday: productActions.getBillToday,
};

const connectedProduct = connect(mapState, actionCreators)(Reports);
export { connectedProduct as Reports };
